
.add-option-error{
    color: $off-white;
    font-style: italic;
    padding: 0 $m-size;
    margin: $m-size 0 0 0;
}

.add-option{
    display: flex;
    padding: $m-size;
}

.add-option__input{
    background: $dark-blue;
    border: none;
    border-bottom: .3rem solid darken($dark_blue, 10%);
    flex-grow: 1;
    color: $off-white;
    padding: $s-size;
    margin-right:  $s-size;
}